import React from 'react';

import QrScanner from 'qr-scanner';
import connect from '@vkontakte/vk-connect';

import { Panel, PanelHeader, File, Div, View, Touch, Button, Link, Snackbar, Group } from '@vkontakte/vkui';

import Linkify from 'react-linkify';

import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import Icon24Fullscreen from '@vkontakte/icons/dist/24/fullscreen';
import Icon24Qr from '@vkontakte/icons/dist/24/qr';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24DoNotDisturb from '@vkontakte/icons/dist/24/do_not_disturb';

import '@vkontakte/vkui/dist/vkui.css';
import './css/main.css';


class App extends React.Component {
	constructor (props) {
		super(props);
		
		this.defaultState = {
			image: '',
			cropperStyles: {},
			croppedImageStyles: {},
			cropperImageStyles: {},
			croppedImageOverflowSyles: {},
			currentPosCroppedX: 0,
			currentPosCroppedY: 0,
			currentPosImageX: 0,
			currentPosImageY: 0,
			currentHeightCropper: 100,
			currentWidthCropper: 100,
			wrapCropperStyles: {},
			wrapCropperContentStyles: {},
			qrResult: "",
		}

		this.state = {
			...this.defaultState,
			aciveSnack: null
		}
		
		this.currentCroppedX = this.state.currentPosCroppedX;
		this.currentCroppedY = this.state.currentCroppedY;

		this.currentPosImageX = this.state.currentPosImageX;
		this.currentPosImageY = this.state.currentPosImageY;

		this.currentHeightCropper = this.state.currentHeightCropper;
		this.currentWidthCropper = this.state.currentWidthCropper;
		this.lastScrollPos = 0;
		
		this.snacks = {
			qrDecodedSuccessfull: this.qrDecodedSnack(),
			qrDecodedError: this.qrDecodedSnack(false)
		}

		QrScanner.WORKER_PATH = 'libs/qr-scanner/qr-scanner-worker.min.js';
	}

	setImage (src="", readQr=true) {
		let img = new Image();
		img.src = src;
		img.onload = () => {
			
			
			let imgHeight = img.height*window.innerWidth/img.width;;


			let currentPosCroppedX = window.innerWidth / 2 - this.defaultState.currentWidthCropper / 2;
			let currentPosCroppedY = (imgHeight / 2) - this.defaultState.currentHeightCropper / 2;

			this.setState({
				image: src,
				imageWidth: img.width,
				imageHeight: img.height,
				currentPosCroppedX,
				currentPosCroppedY,
				croppedImageStyles: {
					transform: `translateX(${currentPosCroppedX}px) translateY(${currentPosCroppedY}px)`
				},
				croppedImageOverflowSyles: {
					transform: `translateX(${-(currentPosCroppedX)}px) translateY(${-(currentPosCroppedY)}px)`
				}
			});
		}
	}

	changeSelectedPhoto = (event) => {
		let input = event.target;
		this.removeStopScrollWindow();
		
		this.setState({
			...this.state,
			...this.defaultState
		});


		if (input && input.files && input.files[0]) {
			let fr = new FileReader();
			
			fr.onload = (e) => {
				this.setImage(e.target.result);
			}

			fr.onerror = () => {
				console.error('Файл не прочитан!');
			}

			fr.readAsDataURL(input.files[0]);
		}
	}

	seActiveSnack (snack) {
		this.setState({
			aciveSnack: snack
		});
	}

	qrDecodedSnack (success=true) {
		return (<Snackbar onClose={() => this.seActiveSnack(null)} before={success ? <Icon24Done fill="#16C60C"/> : <Icon24DoNotDisturb fill="#F44336"/> }>{success ? "Данные получены" : "QR не найден"}</Snackbar>);
	}

	componentDidUpdate () {
		if (this.state.image) {
			let imgNode = document.getElementById("opacity-image");
			if (imgNode && this.state.cropperStyles.height !== imgNode.offsetHeight) {
				this.setState({
					cropperStyles: {
						...this.state.cropperStyles,
						height: imgNode.height
					}
				})
			}			
		}
	}


	move = (e, x, y) => {
		let newCurrentX = this.state.currentPosCroppedX + e.shiftX;
		let newCurrentY = this.state.currentPosCroppedY + e.shiftY;
		
		if (newCurrentX < 0) newCurrentX = 0;
		if (newCurrentY < 0) newCurrentY = 0;
		
		let imgNode = document.getElementById("opacity-image");

		if (imgNode && newCurrentY + this.state.currentHeightCropper > imgNode.offsetHeight) {
				newCurrentY = imgNode.offsetHeight - this.state.currentHeightCropper;
		}
		
		if (imgNode && newCurrentX + this.state.currentWidthCropper > imgNode.offsetWidth) {
				newCurrentX = imgNode.offsetWidth - this.state.currentWidthCropper;
		}


		this.currentPosCroppedX = newCurrentX;
		this.currentPosCroppedY = newCurrentY;

		this.setState({
			croppedImageStyles: {
				...this.state.croppedImageStyles,
				transform: `translateX(${newCurrentX}px) translateY(${newCurrentY}px)`
			},
			croppedImageOverflowSyles: {
				...this.state.croppedImageOverflowSyles,
				transform: `translateX(${-(newCurrentX) + this.state.currentPosImageX}px) translateY(${-(newCurrentY ) + this.state.currentPosImageY}px)`
			}
		});

	}

	stopScrollWindow = () => {
		console.log('Stopping scroll..')
		
		console.log(window.scrollY)
		
		this.lastScrollPos = window.scrollY;

		this.setState({
			wrapCropperStyles: {
				...this.state.wrapCropperStyles,
				overflow: "hidden",
				transform: `translateY(-${window.scrollY}px)`
			}
		});
		
		document.body.classList.add("no-overflow");
		document.getElementsByTagName("html")[0].classList.add("no-overflow");
	}

	removeStopScrollWindow = () => {
		document.body.classList.remove("no-overflow");
		document.getElementsByTagName("html")[0].classList.remove("no-overflow");

		window.scrollTo(0, this.lastScrollPos);
		this.setState({
			wrapCropperStyles: {
				...this.state.wrapCropperStyles,
				overflow: "auto",
				transform: "none"
			}
		});
	}

	onEndCrop = () => {
		this.setState({
			currentPosCroppedX: this.currentPosCroppedX,
			currentPosCroppedY: this.currentPosCroppedY
		});

		this.removeStopScrollWindow();
	}

	moveImage = (e) => {
		let newCurrentX = this.state.currentPosImageX + e.shiftX;
		let newCurrentY = this.state.currentPosImageY + e.shiftY;

		this.currentPosImageX = newCurrentX;
		this.currentPosImageY = newCurrentY;

		this.setState({
			imageStyles: {
				...this.state.imageStyles,
				transform: `translateX(${newCurrentX}px) translateY(${newCurrentY}px)`
			},
			croppedImageOverflowSyles: {
				...this.state.croppedImageOverflowSyles,
				transform: `translateX(${newCurrentX - this.state.currentPosCroppedX}px) translateY(${newCurrentY - this.state.currentPosCroppedY}px)`
			}
		});
	}

	onEndImageMove = () => {
		this.setState({
			currentPosImageX: this.currentPosImageX,
			currentPosImageY: this.currentPosImageY
		});
	}	

	scaleImagesMove = (e) => {
		let elem = e.originalEvent.target;
		
		let isRight = !!elem.classList.value.match("right");
		let isLeft = !!elem.classList.value.match("left")

		let newCurrentWidth = this.state.currentWidthCropper + (isRight ? e.shiftX : -e.shiftX);
		let newCurrentHeight = this.state.currentHeightCropper + (isRight ? e.shiftY : -e.shiftY);
		
		if (newCurrentWidth < newCurrentHeight) {
			newCurrentHeight = newCurrentWidth;
		} else {
			newCurrentWidth = newCurrentHeight;
		}

		this.currentWidthCropper = newCurrentWidth;
		this.currentHeightCropper = newCurrentHeight;
		
		this.setState({
			cropperImageStyles: {
				...this.state.cropperStyles,
				width: newCurrentWidth,
				height: newCurrentHeight
			},
			croppedImageStyles: {
				...this.state.croppedImageStyles,
				width: newCurrentWidth,
				height: newCurrentHeight
			}
		});

	}

	onEndScaleImagesMove = (e) => {
		this.setState({
			currentWidthCropper: this.currentWidthCropper,
			currentHeightCropper: this.currentHeightCropper
		});
		this.removeStopScrollWindow();
	}

	getQrFromCrop = () => {
		let imgNode = document.getElementById("opacity-image");
		let cropNode = document.getElementById("crop");
		let cropCoords = cropNode.getBoundingClientRect();
		
		let offsetCropX = this.state.currentPosCroppedX;
		let offsetCropY =	this.state.currentPosCroppedY;

		let x = Math.floor(offsetCropX * this.state.imageWidth / imgNode.offsetWidth);
		let y = Math.floor(offsetCropY * this.state.imageHeight / imgNode.offsetHeight);

		let height = Math.floor(this.state.currentHeightCropper * this.state.imageHeight / imgNode.offsetHeight);
		let width = Math.floor(this.state.currentWidthCropper * this.state.imageWidth / imgNode.offsetWidth);

		let canvas = document.createElement("canvas");
		
		canvas.width = this.state.imageWidth;
		canvas.height = this.state.imageHeight;

		let ctx = canvas.getContext('2d');
	

		let img = new Image();
		img.onload = () => {

			ctx.drawImage(img, 0, 0);

			let imageData = ctx.getImageData(x, y, width, height);
			canvas.height = height;
			canvas.width = width;
			ctx.clearRect(0, 0, canvas.width, canvas.height);
			ctx.putImageData(imageData, 0, 0);
			img = new Image();
			
			img.onload = () => {
				this.getQRFromImage(img);
			}

			img.src = canvas.toDataURL();
		}

		img.src = this.state.image;
	}

	getQRFromImage (image) {
		QrScanner.scanImage(image)
    .then(res => {
    	this.setQrResult(res);
    	this.seActiveSnack(this.snacks.qrDecodedSuccessfull);
    })
    .catch(error => {
    	this.setQrResult("");
    	this.seActiveSnack(this.snacks.qrDecodedError);
    });
	}

	setQrResult = (res) => {
		this.setState({
			qrResult: res
		});
	}
	
	openThisImage = () => {
		connect.send("VKWebAppShowImages", {
			images: [this.state.image]
		});
	}


	render () {
		let controllers = ["bottom-right"];

		return (
			<View activePanel="main">
				<Panel id="main" theme="white" className={!this.state.image && "centering-panel"}>
					<PanelHeader>
						QR без камеры
					</PanelHeader>
					{this.state.image && 
						<>
							<div className="wrap-cropper" style={this.state.wrapCropperStyles}>
								<div className="wrap-cropper-content" style={this.state.wrapCropperContentStyles}>
									<div className="cropper" style={this.state.cropperStyles}>
											
											<div className="crop" id="crop" style={this.state.croppedImageStyles}>
												{controllers.map(controllerType => {
													return (<Touch onStart={this.stopScrollWindow} onMove={this.scaleImagesMove} onEnd={this.onEndScaleImagesMove}>{<div className={"crop-controller " + controllerType}></div>}</Touch>);
												})}

												<Touch onStart={this.stopScrollWindow} onMove={this.move} onEnd={this.onEndCrop}>
													<div className="cropped-image" style={this.state.cropperImageStyles} onTouchStart={this.startTouch}>
														<img style={this.state.croppedImageOverflowSyles} src={this.state.image}/>
													</div>
												</Touch>
											</div>

										<div className="opacity-image" onClick={this.openThisImage}>
											<img id="opacity-image" style={this.state.imageStyles} src={this.state.image}/>
										</div>
									</div>
									<Div>
										{this.state.qrResult && <div style={{wordBreak: 'break-all', marginTop: 8, marginBottom: 24}}>
											<Linkify componentDecorator={(href, text, key) => {
	                        return (<Link href={href} key={key} target="_blank" rel="noopener noreferrer">{text}</Link>);
	                   }} >{this.state.qrResult.split('\n').map(a => {return (<span><span>{this.state.qrResult}</span><br/></span>)})}</Linkify>
										</div>}
										{!this.state.qrResult && <div style={{wordBreak: 'break-all', marginTop: 8, marginBottom: 24}}>Уточните область QR кода</div>}
										<Button onClick={this.getQrFromCrop} before={<Icon24Qr/>} size="xl" style={{marginBottom: 8}}>Сканировать</Button>
										<File accept="image/x-png,image/jpeg" onChange={this.changeSelectedPhoto}  before={<Icon24Camera/>} size="xl" level="secondary">Изменить изображение</File>
									</Div>
								</div>
							</div>
						</>
					}
					{!this.state.image && <Div>
						<p>Чтобы просканировать QR без камеры, выберите скриншот, где он изображен</p>
						<File accept="image/x-png,image/jpeg" onChange={this.changeSelectedPhoto} before={<Icon24Camera/>} size="l">Выбрать скриншот</File>
					</Div>}
					{this.state.aciveSnack}
				</Panel>
			</View>
		);
	}
}

export default App;

